import getters from './getters.js';

const state={
    headerTxt:"积分商城",
    fanhui:false,
    erweima:false
}

const mutations={
    upCation(state){
        state.headerTxt="分类",
        state.fanhui=true,
        state.erweima=true
    },
    upHome(state){
        state.headerTxt="积分商城",
        state.fanhui=false,
        state.erweima=false
    },
    upShop(state){
        state.headerTxt="购物车",
        state.fanhui=true,
        state.erweima=false
    },
    upCenter(state){
        state.headerTxt="我的",
        state.fanhui=true,
        state.erweima=true
    }
}

export default {
    state,
    mutations,
    getters
}


